    // 获取 保险池+全球节点 那两块
    async getAll3 () {
      let address = await this.$contract.defaultAddress();
      address = address.base58;
      let res = await this.$contract.dayWeekData56(address);
      console.log('All3返回值:', res);
      // 保险池时间和永续池的时间
      let time = this.$util.hexToDec(res._createdAt._hex);
      let downTime = this.$util.myDiv(Date.now().toString(), '1000');
      downTime = this.$util.mySub(downTime, time);
      clearInterval(test3);
      const test3 = setInterval(() => {
        downTime = this.$util.mySub(downTime, '1');
        this.v4 = this.$util.daysFormat(downTime)
      }, 1000);
      // 保险池的usdt数量
      this.pool1 = this.$util.hexTo10To6number3(res._insurance._hex);
      // 永续池的usdt数量
      this.pool2 = this.$util.hexTo10To6number3(res._sustainable._hex);

      // 全球节点
      // 判断结果是否是1,  如果是max=max, min=0; 否则就max=max, min=min;
      let res9 = await this.$contract.teamsCount(address, 0);
      res9 = this.$util.hexToDec(res9._hex);
      if (res9 === '1') {
        // max, small
        this.v5.max = this.$util.hexTo10To6number3(res._max._hex);
        this.v5.small = '0'
      } else {
        // max, small
        this.v5.max = this.$util.hexTo10To6number3(res._max._hex);
        this.v5.small = this.$util.hexTo10To6number3(res._min._hex)
      }
      // 全球节点收益
      this.v5.gloablNodeIncome = this.$util.hexTo10To6number3(
        res._nodeIncome._hex
      );
      // 节点焚毁收益
      this.v5.nodeIcome = this.$util.hexTo10To6number3(res._nodeBurnIncome._hex);
      // 判断是否已经焚毁过, 显示不同的按钮样式
      this.isBurn = await this.$contract.nodeBurnIncome2(address);
      // 未提现收益
      this.v.untakeIncome = this.$util.hexTo10To6number3(res._untakeIncome._hex)
    },

