    // 发送按钮
    async sendOut () {
      if (this.sendLoading) {
        console.log('正在加载, 不能重复点击');
        return
      }
      this.sendLoading = true;
      let number = this.usdtInput;
      if (number === '' || number === '0' || number === 0) {
        console.log('输入框为空');
        this.sendLoading = false;
        return
      }

      // 判断有没有授权ECO
      let res66 = await this.$contract.allowanceEcoMast();
        res66 = this.$util.hexToDec(res66._hex);
      res66 = this.$util.div10e6(res66);
      if (this.$util.myLte(res66, '1')) {
        // 说明没有授权- 这里需要弹框
        this.$message(this.$t('n2'));
        return
      }
      // 判断今天投资数量
      let res55 = await this.$contract.getMaxInvestAmountPerDay();
      res55 = this.$util.mul10e6(this.$util.hexToDec(res55._hex));
      let res44 = await this.$contract.getTotalRevenue();
      res44 = this.$util.hexToDec(res44._hex);
      let res33 = await this.$contract.getYesterRevenue();
      res33 = this.$util.hexToDec(res33._hex);
      console.log("res44  res33 res55:",res44,res33,res55);
      console.log("this.$util.mySub(res44, res33):",this.$util.mySub(res44, res33));
      console.log("res55:",res55);
      //判断时间 getYesterUpdateTime
      let yesterUpdateTime = await this.$contract.getYesterUpdateTime();
      yesterUpdateTime = this.$util.hexToDec(yesterUpdateTime._hex);

      let nowTime = Date.parse((new Date()).toString());
      //测试时用600，生产时用86400
      // let bo = nowTime/1000 < parseInt(yesterUpdateTime) + 600;
      let bo = this.$util.myLt(this.$util.myDiv(nowTime, '1000'), this.$util.myAdd(yesterUpdateTime, this.$util.myTime));
      console.log("nowTime:",nowTime/1000,"yesterUpdateTime+600:",parseInt(yesterUpdateTime) + 86400,"yesterUpdateTime:",yesterUpdateTime,bo);
      console.log("this.$util.mySub(res44, res33) > res55:",this.$util.mySub(res44, res33) > parseInt(res55));
      if(bo && this.$util.myGt(this.$util.mySub(res44, res33), res55)) {
        // 需要弹框
        this.$message(this.$t('n1'));
        return
      }

      // 判断最大限制
      let limit = await this.$contract.maxInvestAmount();
      limit = this.$util.hexToDec(limit._hex);
      if (this.$util.myGt(number, limit)) {
        console.log('投资超出了最大值');
        alert(this.$t('Tips9') + ':' + limit + 'U');
        this.sendLoading = false;
        return
      }
      // 计算推荐人
      let referrer;
      if (this.$route.query.referrer) {
        // 如果url有推荐人地址的话, 就使用这个地址作为推荐人
        referrer = this.$route.query.referrer;
        // 判断url的推荐人地址是否合法
        let urlRes = await this.$contract.userData(referrer);
        let id = this.$util.hexToDec(urlRes.id._hex);
        // 如果id大于1推荐人合法, 否则不合法
        if (this.$util.myLt(id, '1')) {
          alert(this.$t('Tips4'));
          this.sendLoading = false;
          // return;
        }
      } else {
        // 如果url上没有的话, 就使用默认的推荐人地址;
        referrer = this.$contract.defaultReferrer
      }
      /* // 判断USDT余额是否足够
				let address = await this.$contract.defaultAddress();
				address = address.base58;
				let usdtBalance = await this.$contract.getUsdtBalance(address);
				usdtBalance = this.$util.hexTo10To6number3(usdtBalance._hex);
				console.log(usdtBalance, '=====')
				if(usdtBalance < number + 1000) {
					// 考虑到金额四舍五入的准度问题
					this.sendLoading = false;
					alert(this.$t('Tips3'))
					return;
				}; */
      // 判断ECO余额是否足够
      let address = await this.$contract.defaultAddress();
      address = address.base58;
      let ecoBalance = await this.$contract.getEcoBalance(address);
      ecoBalance = this.$util.hexTo10To18number3(ecoBalance._hex);
      let res9 = await this.$contract.price();
      res9 = this.$util.hexTo10To18number3(res9._hex);
      let c1 = this.$util.myMul(number, res9);
      //let compareNum = this.$util.myMul(c1, 0.05)
      let compareNum = this.$util.myMul(c1, this.$util.myDiv(this.joinBurnRate, '100'));

      console.log('发送按钮:', number, res9, ecoBalance, compareNum);
      // 判断ECO的出售量是否达到了450万, 到达450就提示弹框
      let res450 = await this.$contract.totalVending();
      res450 = this.$util.hexTo10To18number3(res450._hex);
      console.log('是否到达res450:', res450);
      // 全局弹框需要传入的参数
      number = this.$util.mul10e6(number);
      this.boxParame1 = referrer;
      this.boxParame2 = number;

      // 判断波长币够不够执行手续费
      let balance1 = await this.$contract.getTRXBalance(address);
      balance1 = this.$util.div10e6(balance1);
      if (this.$util.myLt(balance1, this.$contract.gTronTips)) {
        alert(this.$t('Tips11'))
      }

      // 正常就直接执行
      // eco余额不够
      console.log('对比余额', ecoBalance, compareNum);
      if (this.$util.myLt(ecoBalance, compareNum)) {
        //if(ecoBalance < number * res9 * 0.05) {
        if (this.$util.myGte(res450, '650000')) {//origin 4500000 2800000
          // 给个弹框
          this.isSendoutBox = true
        } else {
          // 考虑到金额四舍五入的准度问题
          this.sendLoading = false;
          alert(this.$t('Tips3'));
          return
        }
      } else {
        // eco余额足够
        this.sendOut3()
      }
    },
