    /* 第一块; v1...v5 */
    async getV1 () {
      // 获取钱包默认的地址
      let address = await this.$contract.defaultAddress();
      address = address.base58;
      let res;
      for (let i = 0; i < 3; i++) {
        res = await this.$contract.userData(address);
        if (this.$util.myLt(this.$util.hexToDec(res.lastTakeTime._hex), '100000000000')) {
          break
        }
      }
      console.log('主页getV1返回值:', res);
      // 是否高亮地址
      this.isHightAddress = !(this.$util.hexToDec(res.id._hex) === '0');
      // this.isHightAddress = this.$util.hexToDec(res.id._hex) === '0' ? false : true;

      // 数据
      let joinTime = this.$util.hexToDec(res.joinTime._hex);
      // let downTime = Math.floor(joinTime + 86400 * 100 - Date.now() / 1000);
      let a8 = this.$util.myAdd(joinTime, '8640000');
      let a9 = this.$util.myDiv(Date.now().toString(), '1000');
      let downTime = this.$util.mySub(a8, a9);
      downTime = Math.floor(downTime);

      // 默认清楚上一个倒计时, 如果没有也不会报错
      clearInterval(this.timer1);
      this.timer1 = setInterval(() => {
        downTime = this.$util.mySub(downTime, '1');
        // v1右边的100天倒计时
        this.v.cd100DayString = this.$util.dayTime2(downTime)
      }, 1000);
      // 已提现收益
      //let a99 = await this.$contract.takedIncome(address);
      let a99 = res.income._hex;
      this.v.alreadyIncome = this.$util.hexTo10To6number3(a99);

      // 日奖池收益
      this.v.dayPoolIncome = this.$util.hexTo10To6number3(
        res.dayPoolIncome._hex
      );
      // 周奖池收益
      this.v.weekPoolIncome = this.$util.hexTo10To6number3(
        res.weekPoolIncome._hex
      );
      // 动态收益
      this.v.dynamicIncome = this.$util.hexTo10To6number3(
        res.dynamicIncome._hex
      );
      // 如果jointime是0,就不显示倒计时(显示0),
      let lastTakeTime = this.$util.hexToDec(res.lastTakeTime._hex);
      // 测试 %86400
      // let downTime2 = lastTakeTime === 0?'0':Math.floor(86400 - ((Date.now() / 1000 - lastTakeTime) % 86400));
      // 主网 %600
      // let downTime2 = lastTakeTime === '0' ? '0' : Math.floor(600 - ((Date.now() / 1000 - lastTakeTime) % 600));
      let b9 = this.$util.myDiv(Date.now().toString(), '1000');
      b9 = this.$util.mySub(b9, lastTakeTime);
      b9 = b9 % this.$util.myTime;
      b9 = this.$util.mySub(this.$util.myTime, b9);
      b9 = Math.floor(b9);
      let downTime2 = lastTakeTime === '0' ? '0' : b9;
      // 默认清楚上一个倒计时, 如果没有也不会报错
      clearInterval(this.timer2);
      this.timer2 = setInterval(() => {
        downTime2 = this.$util.mySub(downTime2, '1');
        // 下次分红倒计时
        if (this.$util.myGt(this.v.deposit, '0')) {
          this.v.cdTimeString = this.$util.hoursTime(downTime2)
        }

        // ===新加bug==; 如果分红收益>=充值金额的15%, 那么就必须要倒计时变成了0, 否者分红收益不就不可以>=15%;
        if (this.$util.myGt(this.v.cdTimeString, '0') && this.$util.myGte(this.v.reward, this.$util.myMul(this.v.deposit, '0.15'))) {
          this.v.reward = '0'
        }
        // 新bug, 如果倒计时<=0, 并且id!=0的话, 就要从新请求下数据
        if (this.$util.myLte(downTime2, '0') && this.$util.hexToDec(res.id._hex) !== '0') {
          this.init()
        }
      }, 1000);


      // 倒计时分红, 下次分红倒计时右边的数据
      // 测试网 /86400; 主网 = /600;
      let timeGap = this.$util.myTime;
      // let pastDays = Math.floor((Date.now() / 1000 - lastTakeTime) / timeGap);
      let pastDays = this.$util.myDiv(Date.now().toString(), '1000');
      pastDays = this.$util.mySub(pastDays, lastTakeTime);
      pastDays = this.$util.myDiv(pastDays, timeGap);
      pastDays = Math.floor(pastDays);
      if (this.$util.myGte(pastDays, '30')) {
        if (this.$util.hexTo10To6number3(res.deposit._hex) !== '0') {
          pastDays = '30'
        } else {
          pastDays = '0'
        }
        clearInterval(this.timer2)
      }
      this.v.timeReward = this.$util.myLte(lastTakeTime, '0') ? '0' : pastDays;
      // 判断数值溢出
      this.v.timeReward = this.$util.myLte(this.v.timeReward, '0') ? '0' : this.v.timeReward;
      // 存入资金
      this.v.deposit = this.$util.hexTo10To6number3(res.deposit._hex);
      if (this.$util.myEq(this.v.deposit, '0')) {
        // 判断v1..v5的身份
        this.v.nodeLevel = ''
      } else if (this.$util.myLt(this.v.deposit, '1000')) {
        // 判断v1..v5的身份
        this.v.nodeLevel = '1'
      } else if (this.$util.myLt(this.v.deposit, '5000')) {
        this.v.nodeLevel = '2'
      } else if (this.$util.myLt(this.v.deposit, '15000')) {
        this.v.nodeLevel = '3'
      } else {
        this.v.nodeLevel = '4'
      }
      this.v.nodeLevel2 = res.nodeLevel.toString();
      // 加速出局差
      this.v.AcceleratedExit = this.$util.hexToDec(res.maxIncome._hex) === '0' ? '0' : this.$util.mySub(this.$util.hexTo10To6number3(res.maxIncome._hex), this.$util.hexTo10To6number3(a99));
      // 未提现收益
      //this.v.untakeIncome = this.$util.hexTo10To6number3(res.income._hex);
      // 保险池奖励
      this.v.reward2 = this.$util.hexTo10To6number3(res.reward._hex)
    },

    // 日奖池收益提现和周奖池收益提现; 两个按钮同样的方法
    async poolWithdraw1 () {
      if (this.dayWeekLoading1) {
        console.log('不能重复点击');
        return
      }
      this.dayWeekLoading1 = true;
      // 判断波长币够不够执行手续费
      let address = await this.$contract.defaultAddress();
      address = address.base58;
      let balance1 = await this.$contract.getTRXBalance(address);
      balance1 = this.$util.div10e6(balance1);
      if(this.$util.myLt(balance1, this.$contract.gTronTips)) {
        alert(this.$t('Tips11'))
      }
      try {
        let res = await this.$contract.takePoolIncome();
        console.log('v1提现', res)
      } catch (e) {
        this.dayWeekLoading1 = false
      }
      this.init();
      this.dayWeekLoading1 = false
    },
    async poolWithdraw2 () {
      if (this.dayWeekLoading2) {
        console.log('不能重复点击');
        return
      }
      this.dayWeekLoading2 = true;
      // 判断波长币够不够执行手续费
      let address = await this.$contract.defaultAddress();
      address = address.base58;
      let balance1 = await this.$contract.getTRXBalance(address);
      balance1 = this.$util.div10e6(balance1);
      if (this.$util.myLt(balance1, this.$contract.gTronTips)) {
        alert(this.$t('Tips11'))
      }
      try {
        let res = await this.$contract.takePoolIncome();
        console.log('v1提现', res)
      } catch (e) {
        this.dayWeekLoading2 = false
      }
      this.init();
      this.dayWeekLoading2 = false
    },
    // 保险池的提现按钮
    async insurancePoolWithdraw1 () {
      //insurancePoolLoading1
      if (this.insurancePoolLoading1) {
        console.log('不能重复点击');
        return
      }
      this.insurancePoolLoading1 = true;
      // 判断波长币够不够执行手续费
      let address = await this.$contract.defaultAddress();
      address = address.base58;
      let balance1 = await this.$contract.getTRXBalance(address);
      balance1 = this.$util.div10e6(balance1);
      if (this.$util.myLt(balance1, this.$contract.gTronTips)) {
        alert(this.$t('Tips11'))
      }
      try {
        let res = await this.$contract.takeReward();
        console.log('保险池的提现按钮-返回值', res)
      } catch (e) {
        this.insurancePoolLoading1 = false
      }
      this.init();
      this.insurancePoolLoading1 = false
    },
    // 最大的提现按钮
    async smallWithdraw () {
      this.withdrawLoading = true;
      // 如果投资的金额大于已经取现的金额+将要取现的金额, 就不弹框提示
      let address = await this.$contract.defaultAddress();
      address = address.base58;
      let res = await this.$contract.dayWeekData1(address);
      let b = res._bo;
      console.log('最大的提现按钮res:', res);
      // 如果等于ture就显示弹框
      if (b) {
        // 否则就显示弹框
        this.isBlock = true
      } else {
        // false就直接提现
        this.smallWithdraw2()
      }
    },
    // 点击了确认
    async smallWithdraw2 () {
      console.log('开始提现');
      this.isBlock = false;
      // 判断波长币够不够执行手续费
      let address = await this.$contract.defaultAddress();
      address = address.base58;
      let balance1 = await this.$contract.getTRXBalance(address);
      balance1 = this.$util.div10e6(balance1);
      if (this.$util.myLt(balance1, this.$contract.gTronTips)) {
        alert(this.$t('Tips11'))
      }
      let res;
      try {
        await this.$contract.take_master();
        this.withdrawLoading = false;
        await this.init();
        console.log('v12提现', res)
          setTimeout(async () => {
              this.init();
          }, 2000);
          setTimeout(async () => {
              this.init();
          }, 5000);
          setTimeout(async () => {
              this.init();
          }, 9000);

      } catch (e) {
        this.withdrawLoading = false
      }
    },
    // 点击了取消
    smallWithdraw3 () {
      console.log('关闭了弹框');
      this.isBlock = false;
      this.withdrawLoading = false
    },

    /* 第二块-邀请链接 */
    // 点击了复制ESD邀请链接 - 复制
    async e32 () {
      console.log('点击了复制ESD邀请链接 - 复制');
      let value = this.v2.address2;
      let input = document.createElement('input');
      input.value = value;
      document.body.appendChild(input);
      input.select();
      input.setSelectionRange(0, input.value.length);
      document.execCommand('Copy');
      document.body.removeChild(input);
      // 复制弹框
      alert('Copy succeeded')
    },

    /* 第五块-全球节点 */
    // 点击了焚毁按钮
    async nodeBurn () {
      // 判断有没有焚毁过
      if (this.isBurn) {
        console.log('已经焚毁了');
        return
      }
      // 判断重复点击
      if (this.burnLoading) {
        console.log('不能重复点击');
        return
      }
      this.burnLoading = true;
      // 判断ECO余额是否足够
      let address = await this.$contract.defaultAddress();
      address = address.base58;
      // 判断波长币够不够执行手续费
      let balance1 = await this.$contract.getTRXBalance(address);
      balance1 = this.$util.div10e6(balance1);
      if (this.$util.myLt(balance1, this.$contract.gTronTips)) {
        alert(this.$t('Tips11'))
      }
      let ecoBalance = await this.$contract.getEcoBalance(address);
      // ecoBalance = this.$util.hexTo10To18number3(ecoBalance._hex);
      ecoBalance = this.$util.hexToDec(ecoBalance._hex);
      // ecoBalance = this.$util.div10e18(res2);
      // 需要的钱
      let res2 = await this.$contract.price();
      // res2 = this.$util.numberFormat(this.$util.div10e18(this.$util.hexToDec(res2._hex) * 3000));
      res2 = this.$util.hexToDec(res2._hex);
      res2 = this.$util.myMul(res2, '3000');
      // res2 = this.$util.div10e18(res2);
      // res2 = this.$util.numberFormat(res2);
      console.log('点击了焚毁按钮:', ecoBalance, res2);
      // 如果需要的数量是整数数就直接对比
      /*if (Number.isInteger(res2) && ecoBalance >= res2) {
      } else {
        // 如果不是整数的话, 说明可能计算会有误差, 需要多加个0.001;
        if (ecoBalance < res2 + 0.001) {
          // 考虑到金额四舍五入的准度问题
          this.burnLoading = false;
          alert(this.$t('Tips3'));
          return
        }
      }*/
      if(this.$util.myLt(ecoBalance, res2)) {
        this.burnLoading = false;
        alert(this.$t('Tips3'));
        return
      }
      await this.$contract.activateNodeBurn();
      this.burnLoading = false;
      this.init();
    }
  }
}
</script>

<style>
/* 宽度尺度大于1500时 */
@media only screen and (min-width: 415px) {
  .v4 {
    display: flex;
  }
  .v {
    background: #edeef2;
    color: #938aa2;
    padding: 3px 15px;
    margin-right: 20px;
  }

  .v-layout {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
  }

  .v-pot {
    background: #ffffff;
    border-radius: 20px;
    border: 1px #f5f5f5 solid;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 48%;
    height: 180px;
  }
  .logo {
    width: 300px;
    height: 66px;
  }
  .jackpot-layout {
    display: flex;
    justify-content: center;
  }
  .filling-layout {
    width: 75%;
    margin: 0 auto;
    text-align: left;
  }

  /* 输入框 */
  .filling-input-layout {
    display: flex;
  }

  .filling-input:focus {
    outline: none;
  }

  .filling-input {
    padding: 10px 20px;
    font-size: 20px;
    border-radius: 20px 0 0 20px;
    border: 1px solid #f5f5f5;
    height: 30px;
    flex-grow: 6;
  }

  .filling-usdt {
    background: #ffffff;
    line-height: 50px;
    text-align: center;
    flex-grow: 2;
  }

  .filling-approve {
    background: #34cecc;
    color: #ffffff;
    line-height: 50px;
    text-align: center;
    border-radius: 0 20px 20px 0;
    flex-grow: 2;
  }
  .pot1 {
    margin-right: 30px;
  }
}

/* 宽度尺寸小于414时 */
@media only screen and (max-width: 414px) {
  .logo {
    width: 60%;
    height: 60%;
    margin-top: 20px;
  }
  .v {
    background: #edeef2;
    color: #938aa2;
    padding: 3px 15px;
    margin-bottom: 5px;
  }
  .logo {
    width: 60%;
    height: 60%;
    margin-top: 20px;
  }
  .v-layout {
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
  }

  .v-pot {
    background: #ffffff;
    border-radius: 20px;
    border: 1px #f5f5f5 solid;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 160px;
  }
  .filling-layout {
    text-align: left;
  }

  .filling-tips {
    font-size: 15px;
  }

  /* 输入框 */
  .filling-input-layout {
    display: flex;
  }

  .filling-input:focus {
    outline: none;
  }

  .filling-input {
    padding: 8px 15px;
    font-size: 20px;
    border-radius: 20px 0 0 20px;
    border: 1px solid #f5f5f5;
    height: 30px;
    width: 50%;
  }

  .filling-usdt {
    background: #ffffff;
    text-align: center;
    flex-grow: 1;
    line-height: 46px;
  }

  .filling-approve {
    background: #34cecc;
    color: #ffffff;
    text-align: center;
    border-radius: 0 20px 20px 0;
    flex-grow: 1;
    line-height: 46px;
  }
.jackpot-pot {
    width: 100% !important;
  }

  .pot1 {
    margin-bottom: 20px;
  }
}
/* -----------周日奖池------------ */
/* 默认样式 */
.jackpot-layout {
  margin: 20px 0;
}

.jackpot-pot {
  width: 239px;
  height: 159px;
  background: #ffffff;
  border-radius: 20px;
  border: 1px #f5f5f5 solid;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.jackpot-pot > div:nth-child(1) {
  font-weight: bold;
}

.jackpot-pot > div:nth-child(3) {
  width: 80%;
  text-align: center;
}
/* ------------输入框和选择U---------- */
.filling-layout {
  display: flex;
  flex-direction: column;
}

.filling-layout > div {
  margin-top: 10px;
}

.filling-u {
  display: flex;
  overflow-y: auto;
  margin-bottom: 30px;
}

.colorU {
  border: 1px solid #e0e0e0;
  margin-right: 10px;
  padding: 1px 5px;
  color: #808080;
  cursor: pointer;
}

.colorU2 {
  border: 1px solid #34cecc;
  margin-right: 10px;
  padding: 1px 5px;
  color: #34cecc;
  cursor: pointer;
}

.filling-approve2 {
  background: #ffb400;
  color: #ffffff;
  text-align: center;
  margin-left: 20px;
  border-radius: 10px;
  line-height: 50px;
  min-width: 100px;
}

/* ------------ 余下全部 ------------ */
.v3 > div {
  margin-bottom: 20px;
}

.v-pot > div:nth-child(3) {
  width: 80%;
  text-align: center;
}
</style>



